<template>
  <div id="app">
    <iheader :seller="seller"></iheader>
    <div class="tab border1px">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评价</router-link>
      </div>
    </div>

    <router-view :seller="seller"></router-view>

  </div>
</template>

<script>
  import iheader from "./components/header/header.vue"
  export default {
    name: 'app',
    data(){
      return{
       seller:Object
      };
    },
    created() {
      this.$http.get('/api/seller').then(response => {
        this.seller = response.body.seller;
      });
    },
    components:{
      iheader:iheader
    }

  }
</script>

<style lang='scss' scoped>
  @import './common/common.scss';

.tab{
  height: 40px;
  display: flex;
  background: white;
  line-height: 40px;
  @include border1px(rgba(7,17,27,.1));
  .tab-item{
    flex:1;
    text-align: center;
    &>a{
      display: block;
      height: 100%;
      font-size: 14px;
      color:rgb(77,85,93);
      &.active{
        color: rgb(240,20,20);
      }
    }
  }
}
</style>



